<template>
  <div id = "single-blog">
    <h1>{{blog.title}}</h1>
    <article>{{blog.content}}</article>
    <p>作者: {{blog.content}}</p>
    <p>分类:</p>
    <ul>
      <li v-for="category in blog.categories">
        {{category}}
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name:"single-blog",
  data() {
    return {
      id:this.$route.params.id,
      blog:{}
    }
  },
  created() {
    this.$http.get("https://my-blog-b77e1-default-rtdb.firebaseio.com/posts/"+this.id+".json")
    .then(function(data){
      // console.log(data);
      return data.json();
    }).then(function(data){
      this.blog=data;
    })
  },
}
</script>
<style lang="">
  #single-blog{
    min-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background: #eee;
    border: 1 dotted #aaa;
  }
</style>


